.flexbox{
  display: flex;
  position: relative;
  max-height: 900px;
}

.page_not_found{
  position: fixed;
  width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background-color: white;

}
@media only screen and (max-width: 1130px) {
  .flexbox{
    max-height: none;
  }
}
